#nextButton,
#backButton {
  font-family: "Open Sans";
}
#calendar-header {
  padding: 10px;
  color: #e0ba8f; /* #d36c6c */
  font-size: 28px;
  font-family: "Poppins";
  font-weight: 600;
  display: flex;
  justify-content: space-between;
}
#calendar-header button {
  background-color: #7ea0d6;
}
#calendar-container {
  width: 770px; /* 770 */
  margin-left: 312px;
}
#weekdays {
  width: 100%;
  display: flex;
  color: #247ba0;
}
#weekdays div {
  width: 100px;
  padding: 10px;
  font-family: "Open Sans";
}
#calendar {
  width: 100%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}
.day {
  width: 105px;
  padding: 10px;
  height: 105px;
  cursor: pointer;
  box-sizing: border-box;
  background-color: white;
  margin: 2.5px;
  box-shadow: 0px 0px 3px #cbd4c2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: "Assistant";
}
.day:hover {
  background-color: #ff8a242d;
}
.day + .currentDay {
  background-color: #e8f4fa;
}
.event {
  font-size: 10px;
  font-family: "Assistant";
  padding: 3px;
  background-color: #58bae4;
  color: white;
  border-radius: 5px;
  max-height: 55px;
  overflow: hidden;
}
.event:hover {
  background-color: #daad5b88 !important;
}
.padding {
  cursor: default !important;
  box-shadow: none !important;
}
#eventModal {
  z-index: 20;
  padding: 25px;
  background-color: #edf2f7; /* #e8f4fa */
  box-shadow: 0px 0px 3px black;
  border-radius: 5px;
  width: 350px;
  top: 100px;
  left: calc(50% - 175px);
  position: absolute;
  font-family: "Montserrat";
}
#eventTitleInput {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 3px;
  outline: none;
  border: none;
  box-shadow: 0px 0px 3px gray;
}
#startTimeInput {
  padding: 10px;
  width: 32%;
  box-sizing: border-box;
  margin-top: 22px;
  margin-bottom: 25px;
  margin-right: 5px;
  border-radius: 3px;
  outline: none;
  border: none;
  box-shadow: 0px 0px 3px gray;
  vertical-align: middle;
}
#endTimeInput {
  padding: 10px;
  width: 32%;
  box-sizing: border-box;
  margin-top: 22px;
  margin-bottom: 25px;
  margin-left: 5px;
  border-radius: 3px;
  outline: none;
  border: none;
  box-shadow: 0px 0px 3px gray;
  vertical-align: middle;
}
#eventTitleInput::placeholder,
#eventTitleInput.error::placeholder,
#locationInput::placeholder,
#locationInput.error::placeholder {
  color: #8e8e8e;
}
#eventTitleInput.error,
#startTimeInput.error,
#endTimeInput.error,
#locationInput.error {
  border: 2px solid #eb5454;
}
#locationInput {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 25px;
  border-radius: 3px;
  outline: none;
  border: none;
  box-shadow: 0px 0px 3px gray;
}
#colorInput {
  width: 35px;
  height: 35px;
  margin-top: 22px;
  margin-left: 20px;
  margin-bottom: 25px;
  border-radius: 3px;
  border: none;
  outline: none;
  cursor: pointer;
  box-shadow: 0px 0px 3px gray;
  vertical-align: middle;
}
#privateInput {
  margin-bottom: 25px;
  margin-right: 8px;
}
.privateInput-label {
  font-family: "Assistant";
}
#saveButton {
  margin-right: 2.5px;
  background-color: #7ea0d6;
  width: 75px;
  cursor: pointer;
  box-shadow: 0px 0px 2px gray;
  border: none;
  outline: none;
  padding: 5px;
  border-radius: 5px;
  color: white;
  float: left;
}
#deleteButton {
  margin-right: 2.5px;
  background-color: #d36c6c;
  width: 75px;
  cursor: pointer;
  box-shadow: 0px 0px 2px gray;
  border: none;
  outline: none;
  padding: 5px;
  border-radius: 5px;
  color: white;
}
#cancelButton {
  margin-left: 2.5px;
  background-color: #bdbdbd;
  width: 75px;
  cursor: pointer;
  box-shadow: 0px 0px 2px gray;
  border: none;
  outline: none;
  padding: 5px;
  border-radius: 5px;
  color: white;
  float: right;
}
#saveButton,
#deleteButton,
#cancelButton {
  font-family: "Open Sans";
}
#eventText {
  font-size: 14px;
}
#modalBackDrop {
  top: 0px;
  left: 0px;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
}

/*This changes the color of the leave/delete button for users*/
.delete-leave p {
  background-color: #eb5454;
}

.auth__form-container_error-message {
  color: #eb5454;
  border: 1px solid #eb5454;
  font-family: "Assistant";
  background-color: #f170704d;
  padding: 8px;
  border-radius: 5px;
  width: 84%;
}

.auth__form-container_fields-content_username-check span {
  display: block;
  margin-top: 5px;
  border-radius: 3px;
  padding-left: 3px;
  font-family: "Assistant";
  width: 86.8%;
}

.auth__form-container_fields-content_username-check span.red {
  color: #eb5454;
  border: 1px solid #eb5454;
  background-color: #f170704d;
}

.auth__form-container_fields-content_username-check span.green {
  color: #28a745;
  border: 1px solid #28a745;
  background-color: #28a7454d;
}
#profileModal {
  z-index: 5;
  padding: 15px;
  padding-top: 10px;
  background-color: #ffffff;
  box-shadow: 0px 0px 3px #cbd7df;
  border-radius: 5px;
  width: 250px;
  top: 62px; /* 100 px */
  left: 72px;
  position: absolute;
}

.profile_header {
  display: flex;
  justify-content: left;
  color: #1a1a1a;
  margin-left: 5%;
  font-family: "Montserrat";
}

.profile_avatar {
  display: flex;
  justify-content: center;
}

.profile_full-name {
  margin-top: 7px;
  font-size: 18px;
  font-weight: bold;
  color: #1a1a1a;
  display: flex;
  justify-content: center;
  font-family: "Montserrat";
}

.profile_username {
  margin-top: 5px;
  font-size: 15px;
  font-weight: bold;
  color: #1a1a1a;
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
  font-family: "Montserrat";
}

.profile_phone-number {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 15px;
  border-radius: 3px;
  outline: none;
  border: none;
  box-shadow: 0px 0px 2px gray;
}

.profile_avatar-url {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 25px;
  border-radius: 3px;
  outline: none;
  border: none;
  box-shadow: 0px 0px 2px gray;
}

.profile_phone-number::placeholder,
.profile_avatar-url::placeholder {
  color: #8e8e8e;
}

.profile_updated-message {
  display: flex;
  justify-content: center;
  bottom: 0;
  color: #1a1a1a;
  z-index: 5;
  font-family: "Open Sans";
  font-size: 15px;
  animation: fadeinout 3s ease-in-out forwards;
}

@keyframes fadeinout {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.right_sidebar_container {
  display: flex;
  flex-direction: column;
  right: 0;
  width: 27.5%;
  height: 100%;
  justify-content: start;
  padding: 20px 0px;
  position: absolute;
}

.tab__content {
  width: 100%;
  height: 110%;
}

.tab__buttons {
  display: flex;
  justify-content: end;
  gap: 0px;
  margin: 0px;
}
.tab__button {
  margin-right: 0px;
  width: 100px;
  cursor: pointer;
  border: none;
  outline: none;
  padding: 5px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-color: white;
  color: #7ea0d6;
}
.tab__button.active {
  color: white;
  background-color: #7ea0d6;
}
#viewEventModal {
  z-index: 20;
  padding: 25px;
  background-color: #ffffff;
  box-shadow: 0px 0px 3px black;
  border-radius: 5px;
  width: 350px;
  top: 100px;
  left: calc(50% - 175px);
  position: absolute;
  font-family: "Montserrat";
}
#viewEventModal h2::-webkit-scrollbar {
  display: none;
}
#viewEventModal h2 {
  overflow: auto;
}
.viewEventColor {
  width: 90%;
  height: 25px;
  border-radius: 5px;
}
.viewLocation {
  overflow: auto
}
.viewLocation::-webkit-scrollbar {
  display: none;
}
